.sidebar{
    margin-top: 50px;
    background-color: #040714;
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: start;
    padding-top: 20px;
    padding-left: 20px;
}
.menu-bars{
    margin-left: 2res;
    font-size: 5res;
    background-color:#040714;
}
.nav-menu{
    background-color: #040714;
    width: 200px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 90px;
    left:-100%;
    transition: 850ms;
}
.nav-menu.active{
    left: 0;
    transition: 350ms;
    z-index: 1;
}
.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}
.nav-text a {
    color:#f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0px 16px;
    border-radius: 4px ;
    text-decoration: none;
}
.nav-text a:hover{
background-color: #222;
color: white;
}
.nav-menu-items{
    width: 100%;
    background-color:#040714;
}
.navbar-toggle{
    background-color:#040714;
    width: 50%;
    height: 70px;
    margin-left: 30px;
    display: flex;
    align-items: center;
    justify-content: start;
}
span{
    margin-left: 16px;
}
